          htmlTemplate +=
            '<dt class="dt-img">' + '<img src="' + currentExpert.img_url + '" ' +
            'data-id="' + currentExpert.id + '" alt="researcher image" />' +
            '</dt>' + '<dd><p>' + currentExpert.name + '</p></dd>' +
            '<dd><p><strong>学校</strong>：' + currentExpert.university + '</p></dd>' +
            '<dd><p><strong>学院</strong>：' + currentExpert.college + '</p></dd>' +
            '<dd><p><strong>研究方向</strong>：'
          if (!currentExpert.theme_list || currentExpert.theme_list === '' ||
            currentExpert.theme_list === null || currentExpert.theme_list === undefined) {
            htmlTemplate += '无。</p></dd>'
          } else {
            // 最长显示 20 个字符
            let maxLength = 20
            if (currentExpert.theme_list.length > maxLength) {
              htmlTemplate += currentExpert.theme_list.substring(0, maxLength) + '...</p></dd>'
            } else {
              htmlTemplate += currentExpert.theme_list + '。</p></dd>'
            }
          }
        }
        htmlTemplate += '</dl>'
      }

      // let htmlTemplate = ''
      // favoriteExpertList.forEach(function (item, index) {
      //   htmlTemplate += '<div class="col-md-3">\n' +
      //     '                <h3>' + (item.name || '无数据') + '</h3>\n' +
      //     '                <p>学校：' + (item.university || '无数据') + '</p>\n' +
      //     '                <p>学院：' + (item.college || '无数据') + '</p>\n' +
      //     '                <p>研究方向：' + (item.theme_list || '无数据') + '</p>\n' +
      //     '                <p><a class="expert-detail-anchor btn btn-default" role="button" data-id="' +
      //     item.id + '">查看详情 »</a></p>\n' +
      //     '              </div>\n' +
      //     '              <div class="col-md-1"></div>'
      // })

      // 清空 favorite-expert-list 里面的所有内容为 htmlTemplate
      $('#favorite-expert-list-div div.favorite-expert-list').empty().append(htmlTemplate)

      // 增添点击事件
      $('#favorite-expert-list-div dt.dt-img img').click(function () {
        _this.queryExpertDetailAjax($(this).attr('data-id'))
      })
      $('#favorite-expert-list-div div.favorite-expert-list a.expert-detail-anchor').click(function () {
        _this.queryExpertDetailAjax($(this).attr('data-id'))
      })
    },
    // 用户中心页面初始化
    initUserCenter () {
      let _this = this

      _this.showInfo()

      $('a.show-info').click(function () {
        _this.showInfo()
      })

      $('a.show-expert').click(function () {
        _this.showExpert()
      })
    },
    // 展示用户信息
    showInfo () {
      $('#editMess').css({'display': 'block'})
      $('#favorite-expert-article').css({'display': 'none'})
    },
    // 展示关注学者
    showExpert () {
      $('#editMess').css({'display': 'none'})
      $('#favorite-expert-article').css({'display': 'block'})
    },
    // 退出登录
    logout () {
      console.log('logout ()')
    },
    // 查询专家详情
    queryExpertDetailAjax (expertId) {
      // 切换路由
      let query = 'expert_detail/' + expertId
      // this.pushState(query)
      this.$router.push('/' + query)
    },
    // 平滑移动到锚点
    scrollAnchor (anchorId) {
      $('html, body').animate({
        scrollTop: $('#' + anchorId).offset().top - 3 * 16
      }, 600)
    }
  }
}
</script>

<style>
  div#user-center article.main-module div.ccn-selection-list ul {
    margin: 0;
    padding: 2.5em 3em 1.5em;
  }

  div.user-center-container {
    margin-top: 4em;
    margin-bottom: 1em;
  }

  div.user-center-starter {
    padding: 15px;
  }

  /*article#favorite-expert-article {*/
    /*display: none;*/
  /*}*/

  #favorite-expert-pages > ul {
    margin: 0;
    padding: 2.5em 3em 1.5em;
  }

  #favorite-expert-pages > ul li.show-query-result {
    display: table-cell;
    padding: 0.875em 0 0 1em;
    text-align: center;
    vertical-align: middle;
  }

  #favorite-expert-pages > ul li.show-query-result p{
    display: inline-block;
    text-align: center;
  }

  #favorite-expert-pages ul li:nth-child(4) {
    padding: 0.43em 0 0 1em;
  }

  #favorite-expert-jump-input {
    text-align: center;
  }

  #favorite-expert-list-div {
    width: 95%;
    margin: auto;
    /*max-height: 60%;*/
    overflow-y: scroll;
  }

  div#user-center .favorite-expert-list dl.expert-profile {
    width: 18.8%;
    height: 190px;
    padding: 0.2%;
    margin: 1% 0.5% 1% 0.5%;
    float: left;
    position: relative;
    overflow: hidden;
    border: 1px solid #dedede;
  }

  div#user-center .favorite-expert-list dl.expert-profile dt {
    display: inline-block;
    padding: 0;
    margin: 0.5% 0 0.5% 0;
  }

  div#user-center .favorite-expert-list dl.expert-profile dd {
    padding: 0;
    margin: 0.2% 0 0.2% 0;
  }

  div#user-center .favorite-expert-list dl.expert-profile dd > a {
    cursor: pointer;
    padding: 0;
    margin: 0;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
  }

  div#user-center .favorite-expert-list dl.expert-profile dd > p {
    display: block;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
    overflow: hidden;
    font-size: 12px;
    font-style: normal;
    color: #232323;
  }

  div#user-center .favorite-expert-list dl.expert-profile dt.dt-img img {
    width: 50px;
    height: 50px;
    cursor: pointer;
  }
</style>



